import { splitOnSpecialChars } from '../libs/VXsStringUtils';
var WordsManager = new (/** @class */ (function () {
    function WordsManager() {
        this.words = {};
        this.mostCommon = 0;
        this.longest = 0;
    }
    WordsManager.prototype.getWordKey = function (word) {
        var res = {};
        for (var i = 0; i < word.length; i++)
            if (!res[word[i]])
                res[word[i]] = 1;
            else
                res[word[i]]++;
        return res;
    };
    WordsManager.prototype.checkTwoKeys = function (key1, key2) {
        for (var i in key1)
            if ((key2[i] || 0) < key1[i])
                return false;
        return true;
    };
    /** найти самое часто встречающееся слово */
    WordsManager.prototype.getMostCommon = function () {
        var _this = this;
        /** @type {number} */
        this.mostCommon = 0;
        Object.entries(this.words).forEach(function (w) { return _this.mostCommon = _this.mostCommon > w[1] ? _this.mostCommon : w[1]; });
        return this.mostCommon;
    };
    /** найти наидлиннейшее слово */
    WordsManager.prototype.getLongest = function () {
        var _this = this;
        /** @type {number} */
        this.longest = 0;
        Object.entries(this.words).forEach(function (w) { return _this.longest = _this.longest > w[0].length ? _this.longest : w[0].length; });
        return this.longest;
    };
    /**
     * Импорт текста
     * @param {string} text текст
     */
    WordsManager.prototype.importText = function (text) {
        this.words = {};
        var wordArr = splitOnSpecialChars(text);
        for (var i = 0; i < wordArr.length; i++) {
            var word = wordArr[i].toUpperCase();
            if (!this.words[word])
                this.words[word] = 1;
            else
                this.words[word]++;
        }
        this.getMostCommon();
        this.getLongest();
    };
    WordsManager.prototype.getText = function () {
        return Object.entries(this.words)
            .map(function (x) { return x[0]; });
    };
    /**
     * Найти случайное слово
     * @param minLength минимальная длинна слова, по умолчанию поиск по всем словам
     * @return
     */
    WordsManager.prototype.getRndWord = function (minLength) {
        var findedWords = Object.entries(this.words)
            .filter(function (x) { return x[0].length > (minLength || 0); })
            .map(function (x) { return x[0]; });
        return findedWords[Math.floor(Math.random() * findedWords.length)];
    };
    /**
     * Проверить редкость слова
     * @param {string} word слово
     * @return от 0 до 1 на сколько слово редкое:
     * - 1 самое редкое
     * - 0 самое обычное или не найденное
     */
    WordsManager.prototype.checkRarity = function (word) {
        var rarity = this.words[word];
        return rarity > 0 ? (this.mostCommon - rarity) / this.mostCommon : 0;
    };
    /**
     * Проверить стоимость слова
     * @param {string} word
     * @return стоимость слова, учитывается длинна слова и редкость, самое
     * редкое слово стоит в 2 раза дороже самого обыного с той же длинной
     */
    WordsManager.prototype.checkPrice = function (word) {
        var price = this.words[word];
        if (price > 0)
            return word.length + this.checkRarity(word) * this.longest;
        return -1;
    };
    WordsManager.prototype.getWordsByLength = function () {
        var wordsByLength = [];
        for (var word in this.words) {
            if (!wordsByLength[word.length])
                wordsByLength[word.length] = [];
            wordsByLength[word.length].push({
                word: word,
                price: Math.round(this.checkPrice(word) * 100) / 100,
                rarity: Math.round(this.checkRarity(word) * 100),
                isFound: false
            });
        }
        return wordsByLength;
    };
    return WordsManager;
}()))();
export default WordsManager;
