/**
 * Split string on words
 * @param str
 */
export function splitOnSpecialChars(str) {
    var set = new Set([
        "~", "`",
        "!", "@", "#", "$", "%", "^", "&", "*", "(", ")",
        "_", "+", "-", "=",
        "[", "]", "{", "}", "\\", "|",
        ";", "'", ":", "\"",
        ",", ".", "<", ">", "/", "?",
        " ", "\t", "\r", "\n"
    ]);
    var arr = Array.from(str);
    var res = [];
    var buff = [];
    for (var i in arr) {
        if (!set.has(arr[i])) {
            buff.push(arr[i]);
            continue;
        }
        if (0 === buff.length)
            continue;
        res.push(buff.join(""));
        buff = [];
    }
    if (buff.length > 0)
        res.push(buff.join(""));
    return res;
}
